.array-container {
    display: absolute;
    position: center;
    left: 100px;
    top: 20px;
    bottom: 20px;
    padding: 50px;
    height: 750px;
}

.array-bar {
    width: 2px;
    background-color: deepskyblue;
    display: inline-block;
    margin: 0 1px;
    border: 1px solid black;
}

.sort-explanation {
    position: center;
    display: inline-block;
    color: white;
    border: 1px solid black;
    font-weight: bold;
    width: 850px;
}